
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	color: #2c3e50;
	margin-top: 60px;
}
.form-group {
	display: block;
	margin: 0.5rem 0;
}
label {
	text-align: right;
	padding-right: 10px;
	min-width: 130px;
	display: inline-block;
	text-transform: uppercase;
	font-size: 12px;
	letter-spacing: 0.05em;
}
select,
input {
	display: inline-block;
	max-width: 250px;
	width: 100%;
	padding: 0.25rem;
	font-size: 16px;
	border: 1px solid #e0e0e0;
}
